import { useState, useEffect } from 'react';
import { useSearchParams, Link } from 'react-router-dom';
import { supabase } from './supabaseClient';
import './Country.css';

/**
 * Builds the map image URL for a country based on its ID and English name.
 * Mirrors the PHP logic from lbbodies/country.php.
 */
function getMapSrc(id, englishName, type) {
  const dashedName = englishName.toLowerCase().replace(/ /g, '-');

  if (type === 'US State') {
    const small = ['US-DE', 'US-NH', 'US-RI', 'US-VT'];
    const suffix = small.includes(id) ? '' : '-max';
    return `usa/state/${dashedName}/map-of-${dashedName}${suffix}.jpg`;
  }
  if (type === 'US Capital') {
    return `usa/city/${dashedName}/map-of-${dashedName}-max.jpg`;
  }
  if (type === 'British Kingdom') {
    return `uk/${dashedName}/administrative-divisions-map-of-${dashedName}-max.jpg`;
  }

  // One-off overrides
  const overrides = {
    AE: 'uae/map-of-uae.jpg',
    AQ: `${dashedName}/${dashedName}-map-with-country-claims-max.jpg`,
    AU: `${dashedName}/${dashedName}-map-2-max.jpg`,
    AT: `${dashedName}/${dashedName}-map-max.jpg`,
    BS: 'bahamas/map-of-bahamas-max.jpg',
    CD: 'democratic-republic-of-the-congo/map-of-dr-congo-max.jpg',
    CF: 'central-african/map-of-central-african-max.jpg',
    CG: 'republic-of-the-congo/map-of-republic-of-the-congo-max.jpg',
    CI: 'cote-d-ivoire/map-of-cote-d-ivoire-max.jpg',
    'NORTHERN-CYPRUS': 'cyprus/map-of-cyprus-max.jpg',
    CZ: 'czech-republic/map-of-czech-republic-max.jpg',
    GB: 'uk/united-kingdom-map-max.jpg',
    GM: 'gambia/map-of-gambia-max.jpg',
    MK: 'macedonia/map-of-macedonia-max.jpg',
    MM: 'burma/map-of-burma.jpg',
    MH: 'marshall-islands/map-of-marshall-islands-max.jpg',
    NL: 'netherlands/map-of-netherlands-max.jpg',
    'PS-GAZA': 'palestine/map-of-palestine.jpg',
    'PS-WEST-BANK': 'palestine/map-of-palestine.jpg',
    PH: 'philippines/map-of-philippines.jpg',
    SE: `${dashedName}/political-map-of-${dashedName}.jpg`,
    ST: 'sao-tome-and-principe/map-of-sao-tome-and-principe-max.jpg',
    SC: 'seychelles/map-of-seychelles-1000.jpg',
    SB: 'solomon-islands/map-of-solomon-islands-max.jpg',
    TC: 'turks-and-caicos/map-of-turks-and-caicos-max.jpg',
    TL: 'timor-east/map-of-timor-east-max.jpg',
    TR: 'turkey/map-of-turkey-max.jpg',
    US: 'usa/us-map-max.jpg',
  };
  if (overrides[id]) return overrides[id];

  // Countries that use the short (no -max) URL
  const shortIds = [
    'AL','AR','BZ','HR','IE','IL','FI','HI','JP','LI','LU','ME','MZ','NZ','PT','TH','TN','KR','VN',
  ];
  if (shortIds.includes(id)) {
    return `${dashedName}/map-of-${dashedName}.jpg`;
  }

  return `${dashedName}/map-of-${dashedName}-max.jpg`;
}

/**
 * Returns the flag image URL for a country ID.
 */
function getFlagSrc(id) {
  if (id === 'DANISH-REALM') return 'https://flagpedia.net/data/flags/w580/dk.webp';
  if (id === 'KOSOVO') return 'https://flagpedia.net/data/flags/w580/xk.webp';
  if (id === 'PS-WEST-BANK') return 'https://flagpedia.net/data/flags/w580/ps.webp';
  return `https://flagpedia.net/data/flags/w580/${id.toLowerCase()}.webp`;
}

function CountrySearchBar() {
  const [query, setQuery] = useState('');
  const [type, setType] = useState('global');

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      window.location.href = `/search?q=${encodeURIComponent(query.toLowerCase())}&type=${type}`;
    }
  };

  return (
    <div id="search-form">
      <input
        id="searchbar"
        type="search"
        autoComplete="off"
        placeholder="Search"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        onKeyDown={handleKeyDown}
      />
      <select id="type-selector" value={type} onChange={(e) => setType(e.target.value)}>
        <option value="global"></option>
        <option value="country">Country</option>
        <option value="division">Division</option>
        <option value="organization">Organization</option>
        <option value="law">Law</option>
      </select>
      <select id="country-selector">
        <option value="WW"></option>
      </select>
    </div>
  );
}

export default function Country() {
  const [searchParams] = useSearchParams();
  const idParam = (searchParams.get('id') || '').toUpperCase();
  const lang = window.location.host.split('.')[0] || 'en'; // default language

  const [country, setCountry] = useState(null);
  const [translations, setTranslations] = useState({});
  const [divisions, setDivisions] = useState({}); // { childType: [{ id, name }] }
  const [languageNames, setLanguageNames] = useState({}); // { langId: localizedName }
  const [organizations, setOrganizations] = useState([]); // [{ id, name, memberType }]
  const [disputeNames, setDisputeNames] = useState({}); // { countryId: name }
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // ---- Fetch country row + translations ----
  useEffect(() => {
    if (!idParam) {
      setError('No country ID provided. Use ?id=XX in the URL.');
      setLoading(false);
      return;
    }

    async function fetchData() {
      setLoading(true);
      setError(null);

      try {
        // 1. Fetch country row
        const { data: countryRows, error: cErr } = await supabase
          .from('countries')
          .select('*')
          .eq('ID', idParam);

        if (cErr) throw cErr;
        if (!countryRows || countryRows.length === 0) {
          setError(`Country "${idParam}" not found.`);
          setLoading(false);
          return;
        }

        const row = countryRows[0];
        setCountry(row);

        // Parse JSON fields (they may already be objects if Supabase stores jsonb)
        const nameObj = typeof row.name === 'string' ? JSON.parse(row.name) : row.name;
        const langsObj = typeof row.langs === 'string' ? JSON.parse(row.langs) : row.langs;
        const childrenObj = row.children
          ? typeof row.children === 'string' ? JSON.parse(row.children) : row.children
          : null;
        const disputesObj = row.disputes
          ? typeof row.disputes === 'string' ? JSON.parse(row.disputes) : row.disputes
          : null;

        // Store parsed versions back for convenience
        row._name = nameObj;
        row._langs = langsObj;
        row._children = childrenObj;
        row._disputes = disputesObj;
        row._overview = row.overview
          ? typeof row.overview === 'string' ? JSON.parse(row.overview) : row.overview
          : null;
        row._source = row.source
          ? typeof row.source === 'string' ? JSON.parse(row.source) : row.source
          : null;

        setCountry({ ...row });

        // 2. Fetch translations
        const { data: transRows } = await supabase
          .from('translations')
          .select('*')
          .eq('ID', lang);

        if (transRows && transRows.length > 0) {
          const { ID, ...rest } = transRows[0];
          setTranslations(rest);
        }

        // 3. Fetch division names for children
        if (childrenObj) {
          const allChildIds = Object.values(childrenObj).flat();
          if (allChildIds.length > 0) {
            const { data: divRows } = await supabase
              .from('divisions')
              .select('ID, name, type')
              .in('ID', allChildIds);

            const divMap = {};
            if (divRows) {
              for (const d of divRows) {
                const dName = typeof d.name === 'string' ? JSON.parse(d.name) : d.name;
                let label = dName[lang] || dName['en'] || Object.values(dName)[0];
                if (d.type && d.type.includes('Capital')) label += ' (Capital)';
                divMap[d.ID] = label;
              }
            }

            // Build ordered structure
            const divResult = {};
            for (const [childType, ids] of Object.entries(childrenObj)) {
              if (ids && ids.length > 0) {
                divResult[childType] = ids.map((cid) => ({
                  id: cid,
                  name: divMap[cid] || cid,
                }));
              }
            }
            setDivisions(divResult);
          }
        }

        // 4. Fetch language display names
        if (langsObj) {
          const allLangIds = [...new Set(Object.values(langsObj).flat())];
          if (allLangIds.length > 0) {
            const { data: langRows } = await supabase
              .from('languages')
              .select('ID, name, hasFlag')
              .in('ID', allLangIds);

            const lMap = {};
            if (langRows) {
              for (const l of langRows) {
                const lName = typeof l.name === 'string' ? JSON.parse(l.name) : l.name;
                lMap[l.ID] = {
                  displayName: lName[lang] || lName['en'] || Object.values(lName)[0],
                  nativeName: lName[l.ID] || lName['en'] || Object.values(lName)[0],
                  hasFlag: l.hasFlag,
                };
              }
            }
            setLanguageNames(lMap);
          }
        }

        // 5. Fetch organizations that include this country
        const { data: orgRows } = await supabase
          .from('organizations')
          .select('ID, name, children');

        if (orgRows) {
          const matched = [];
          for (const org of orgRows) {
            const members = typeof org.children === 'string' ? JSON.parse(org.children) : org.children;
            if (!members) continue;

            // Check if country is in any member list
            const allMemberIds = Object.values(members).flat();
            if (!allMemberIds.includes(idParam)) continue;

            // Determine membership type
            let memberType = '';
            for (const [key, ids] of Object.entries(members)) {
              if (key !== 'Members' && ids.includes(idParam)) {
                if (key === 'Suspended') memberType = ' (Suspended)';
              }
            }

            const oName = typeof org.name === 'string' ? JSON.parse(org.name) : org.name;
            matched.push({
              id: org.ID,
              name: oName[lang] || oName['en'] || Object.values(oName)[0],
              memberType,
            });
          }
          setOrganizations(matched);
        }

        // 6. Fetch dispute country names
        if (disputesObj) {
          const disputeIds = [...new Set(Object.values(disputesObj).flat())];
          if (disputeIds.length > 0) {
            const { data: dRows } = await supabase
              .from('countries')
              .select('ID, name')
              .in('ID', disputeIds);

            const dMap = {};
            if (dRows) {
              for (const d of dRows) {
                const dName = typeof d.name === 'string' ? JSON.parse(d.name) : d.name;
                dMap[d.ID] = dName[lang] || dName['en'] || Object.values(dName)[0];
              }
            }
            setDisputeNames(dMap);
          }
        }
      } catch (err) {
        console.error('Error fetching country data:', err);
        setError(err.message || 'Failed to load country data.');
      } finally {
        setLoading(false);
      }
    }

    fetchData();
  }, [idParam, lang]);

  // ---- Derived values ----
  const name = country?._name?.[lang] || country?._name?.['en'] || '';
  const overview = country?._overview?.[lang] || '';
  const hasMap = country?.hasMap;
  const hasSeal = country?.hasSeal;
  const hasFlag = country?.hasFlag;
  const countryType = country?.type || '';
  const langsObj = country?._langs || {};
  const disputesObj = country?._disputes || {};
  const sourceObj = country?._source || null;
  const englishName = country?._name?.['en'] || '';
  const defaultLang = langsObj?.Display?.[0] || 'en';

  // ---- Page title ----
  useEffect(() => {
    if (name) document.title = `${name} - Lawbrary`;
  }, [name]);

  // ---- Render ----
  if (loading) return <p style={{ textAlign: 'center', marginTop: 160 }}>Loading...</p>;
  if (error) return <p style={{ textAlign: 'center', marginTop: 160, color: 'red' }}>{error}</p>;

  return (
    <div style={{ background: 'LightGrey', minHeight: '100vh' }}>
      {/* ===== TOP BAR ===== */}
      <div id="topdiv">
        {/* Logo */}
        <Link id="logo" to="/">
          <img src="/images/logos/Graphic - Main Ribbon.svg" width="40" alt="Lawbrary" />
        </Link>

        {/* Language selector */}
        <div id="language-div">
          {languageNames[lang]?.hasFlag && (
            <img id="language-flag" height="32" src={`/images/langs/${lang}.png`} alt={lang} />
          )}
          <select
            id="language-selector"
            value={lang}
            onChange={(e) => {
              const newLang = e.target.value;
              const params = new URLSearchParams(searchParams);
              params.set('lang', newLang);
              window.location.search = params.toString();
            }}
          >
            {(langsObj.Display || []).map((lid) => (
              <option key={lid} value={lid}>
                {languageNames[lid]?.nativeName || lid}
              </option>
            ))}
          </select>
        </div>

        {/* Title + flag */}
        <div id="title-div">
          <div id="title-text">
            <h1 id="title">
              {hasFlag && (
                <img
                  id="title-flag"
                  height="21.5"
                  src={getFlagSrc(idParam)}
                  alt={`${name} flag`}
                />
              )}{' '}
              {name}
            </h1>
            <br /><br /><br />
            {/* Source website links */}
            {sourceObj && Object.entries(sourceObj).map(([siteNum, siteMap]) => {
              const url = siteMap[lang] || siteMap[defaultLang] || Object.values(siteMap)[0];
              if (!url) return null;
              const domain = url.split('/')[2];
              return (
                <span key={siteNum}>
                  <a id={`source-website-${siteNum}`} href={url} target="_blank" rel="noreferrer">
                    {domain}
                  </a>{' '}
                </span>
              );
            })}
            {sourceObj && <br />}
          </div>
        </div>

        {/* Navbar */}
        <div id="navbar">
          <ul id="navlist">
            <li className="navbutton">
              <Link className="navlink" to={`/country?id=${idParam.toLowerCase()}&lang=${lang}`}>
                {translations.OVERVIEW || 'Overview'}
              </Link>
            </li>
            <li className="navbutton">
              <Link className="navlink" to={`/country/constitution?id=${idParam.toLowerCase()}&lang=${lang}`}>
                {translations.CONSTITUTION || 'Constitution'}
              </Link>
            </li>
            <li className="navbutton">
              <Link className="navlink" to={`/country/case-laws?id=${idParam.toLowerCase()}&lang=${lang}`}>
                {translations.CASE_LAWS || 'Case Laws'}
              </Link>
            </li>
            <li className="navbutton">
              <Link className="navlink" to={`/country/laws?id=${idParam.toLowerCase()}&lang=${lang}`}>
                {translations.LAWS || 'Laws'}
              </Link>
            </li>
          </ul>
        </div>

        {/* Search bar */}
        <CountrySearchBar />
      </div>

      {/* ===== LEFT PANEL ===== */}
      <div id="leftdiv">
        {/* Country map */}
        {hasMap && (
          <img
            id="mapImg"
            src={`https://ontheworldmap.com/${getMapSrc(idParam, englishName, countryType)}`}
            useMap="#Map"
            alt={
              translations.MAP_OF
                ? translations.MAP_OF.replace('[name]', name)
                : `Map of ${name}`
            }
          />
        )}
        <map name="Map">{/* Clickable areas would go here */}</map>

        {/* Territorial dispute disclaimer */}
        {disputesObj && Object.keys(disputesObj).length > 0 && (
          <h4 id="disputeDisclaimer">
            {'!!'}
            {Object.entries(disputesObj).map(([text, countryIds], idx) => {
              if (!countryIds) return null;
              return (
                <span key={idx}>
                  {idx === 0 && `${name} `}
                  {text}
                  {countryIds.map((cid, i) => (
                    <span key={cid}>
                      {i > 0 && ' and '}
                      {' '}
                      <Link to={`/country?id=${cid.toLowerCase()}&lang=${lang}`}>
                        {disputeNames[cid] || cid}
                      </Link>
                    </span>
                  ))}
                  {idx < Object.keys(disputesObj).length - 1 && ' and '}
                </span>
              );
            })}
            {'!!'}
          </h4>
        )}

        {/* List of divisions (children) */}
        {Object.entries(divisions).map(([childType, items]) => (
          <div key={childType}>
            <h2>{childType}</h2>
            {items.map((item) => (
              <span key={item.id}>
                <Link to={`/division?id=${item.id.toLowerCase()}&lang=${lang}`}>
                  {item.name}
                </Link>
                <br />
              </span>
            ))}
            <br />
          </div>
        ))}
      </div>

      {/* ===== CENTER PANEL ===== */}
      <div id="centerdiv">
        {overview && <p id="overview">{overview}</p>}
      </div>

      {/* ===== RIGHT PANEL ===== */}
      <div id="rightdiv">
        {/* Seal / emblem */}
        {hasSeal && (
          <img
            id="seal"
            height="150"
            src={`/images/seals/${idParam}.svg.png`}
            alt={
              translations.SEAL_OF
                ? translations.SEAL_OF.replace('[name]', name)
                : `Seal of ${name}`
            }
          />
        )}

        {/* Country type */}
        {countryType && <p id="country-type">{countryType}</p>}

        {/* Languages */}
        <h2>Languages</h2>
        {Object.entries(langsObj).map(([langType, langIds]) => {
          if (langType === 'Display') return null;
          return (
            <div key={langType}>
              <h3>{langType} languages</h3>
              <ul>
                {langIds.map((lid) => (
                  <li key={lid}>
                    {languageNames[lid]?.displayName || lid}
                    <br />
                  </li>
                ))}
              </ul>
            </div>
          );
        })}

        {/* Organizations */}
        {organizations.length > 0 && (
          <>
            <h2 id="organizationsHeading">Organizations</h2>
            {organizations.map((org) => (
              <span key={org.id}>
                <Link className="orgLink" to={`/organization?id=${org.id.toLowerCase()}&lang=${lang}`}>
                  {org.name}{org.memberType}
                </Link>
                <br />
              </span>
            ))}
          </>
        )}
      </div>
    </div>
  );
}
